(function(e){
  //check if mobile
  if(typeof cordova == "undefined" && /(iPhone|iPad|Android)/.test(navigator.userAgent)){
    window.cordova  = true;
    e.setAttribute("src","{!baseUrl!}cordova.js");
    var s = document.getElementsByTagName("script")[0];
    var h = document.getElementsByTagName("head")[0];
    h.insertBefore(e,s.nextSibling);
  }
})(document.createElement("script"));